        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tile-single</span> <span class="hljs-attr">:url</span>=<span class="hljs-string">"url"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tile-single</span>&gt;</span>
