<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>指定服务地址。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>图层的矩形范围,此矩形限制了影像可见范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">optional</code>指定服务的描述信息。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>参考椭球体。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/SingleTileImageryProvider.html">SingleTileImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者准备好时触发。</td>
</tr>
</tbody>
</table>
